'use client'; import { EquipmentStatus } from '@/lib/types'; const color:Record<EquipmentStatus,string>={OK:'background:#22c55e;color:#04110a;',MAINTENANCE:'background:#f59e0b;color:#1a1206;',FAULT:'background:#ef4444;color:#1a0a0a;',EMERGENCY:'background:#be123c;color:#fff1f2;',OFFLINE:'background:#94a3b8;color:#0b1015;'}; export function StatusBadge({status}:{status:EquipmentStatus}){ const style=Object.fromEntries(color[status].split(';').filter(Boolean).map(s=>s.trim().split(':'))); return <span className='badge' style={style as any}><i className='dot' style={{background:'currentColor'}}/> {status}</span>; }
